body {
  background-color: #0f0a0a;
  color: #c7ad88;
  font-family: 'Times New Roman', Times, serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M50 50L20 80M50 50L80 20M50 50L20 20M50 50L80 80' stroke='%23352c21' stroke-width='1' fill='none'/%3E%3C/svg%3E");
}

/* Navbar Styles */
.navbar {
  background-color: #171412;
  border-bottom: 1px solid #5a4c3b;
  padding: 10px 20px;
  text-align: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar a {
  color: #c7ad88;
  padding: 14px 20px;
  text-decoration: none;
  font-size: 18px;
  display: inline-block;
  transition: all 0.3s ease-in-out;
  font-variant: small-caps;
  letter-spacing: 2px;
}

.navbar a:hover {
  background-color: #251e1a;
  color: #e2c799;
  border-bottom: 1px solid #8b6c42;
}

/* Occult Text */
.occult-text {
  font-size: 48px;
  color: #b79768;
  text-align: center;
  margin: 20px 0;
  font-family: 'Times New Roman', Times, serif;
  font-weight: normal;
  letter-spacing: 4px;
  text-transform: uppercase;
  position: relative;
}

.occult-text::before,
.occult-text::after {
  content: 'âœ§';
  font-size: 28px;
  color: #8b6c42;
  margin: 0 15px;
  vertical-align: middle;
  display: inline-block;
}

/* Projects Section */
.projects-section {
  padding: 40px 20px;
  text-align: center;
}

.projects-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.card {
  background-color: #171412;
  border: 1px solid #5a4c3b;
  padding: 20px;
  width: 300px;
  border-radius: 3px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='50' height='50' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='50' cy='50' r='40' stroke='%23352c21' stroke-width='1' fill='none'/%3E%3C/svg%3E");
  opacity: 0.1;
  z-index: 1;
  pointer-events: none;
}

.card:hover {
  box-shadow: 0 5px 15px rgba(139, 108, 66, 0.5);
  transform: translateY(-5px);
}

.card h3 {
  color: #b79768;
  margin-bottom: 10px;
  position: relative;
  z-index: 2;
}

.card p {
  color: #c7ad88;
  font-size: 16px;
  margin-bottom: 15px;
  font-style: italic;
  position: relative;
  z-index: 2;
}

.card a {
  color: #8b6c42;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
  border-bottom: 1px dotted #8b6c42;
  padding-bottom: 2px;
  position: relative;
  z-index: 2;
}

.card a:hover {
  color: #d4b483;
  border-bottom: 1px solid #d4b483;
}

/* Footer Styles */
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #0f0a0a;
  color: #c7ad88;
  text-align: center;
  padding: 10px;
  font-size: 14px;
  border-top: 1px solid #5a4c3b;
}

/* Contact Section */
#contact {
  padding: 40px 20px;
  text-align: center;
  padding-bottom: 70px;
}

#contact a {
  color: #8b6c42;
  text-decoration: none;
  font-size: 16px;
  border-bottom: 1px dotted #8b6c42;
  padding-bottom: 2px;
  transition: all 0.3s ease;
}

#contact a:hover {
  color: #d4b483;
  border-bottom: 1px solid #d4b483;
}

/* About Section */
#about {
  padding: 40px 20px;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

#about::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 50%;
  width: 200px;
  height: 1px;
  background: linear-gradient(to right, transparent, #5a4c3b, transparent);
  transform: translateX(-50%);
}

#about::after {
  content: '';
  position: absolute;
  bottom: 20px;
  left: 50%;
  width: 200px;
  height: 1px;
  background: linear-gradient(to right, transparent, #5a4c3b, transparent);
  transform: translateX(-50%);
}

#about p {
  font-size: 18px;
  margin: 20px 0;
  line-height: 1.6;
  font-style: italic;
}

/* Responsiveness */
@media (max-width: 768px) {
  .projects-container {
    flex-direction: column;
    align-items: center;
  }

  .card {
    width: 90%;
  }
  
  .occult-text {
    font-size: 36px;
  }
  
  .occult-text::before,
  .occult-text::after {
    font-size: 20px;
    margin: 0 8px;
  }
}
