.card {
  position: relative;
  margin: 20px;
}

/* Add a pentagram pattern to the background of each card on hover */
.card:hover::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M50 10L61 40H94L68 60L79 90L50 75L21 90L32 60L6 40H39L50 10Z' stroke='%23352c21' stroke-width='1' fill='none'/%3E%3C/svg%3E");
  background-size: 100px 100px;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.1;
  z-index: 0;
  pointer-events: none;
}
